.page {

  scroll-behavior: smooth;

  &__body--with-menu {
    overflow: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateY(-120%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }
}
